import miniPro from '../../../utils/wepy-pro';
import { apiSignInF, apiGetSignInfoF, apiExchangF } from '../../../services/memberIntegral';
import { apiInfoF } from '../../../services/cart';
import { apiPasePhoneF } from '../../../services/login/index';

export default {
  data() {
    return {
        Info: {
          integration: "",
          //可用积分
          cumulativeTimes: "",
          signToday: "" //今天是否签到 true\false

        },
        phone: "",
        sessionKey: "",
        dateArr: [],
        IconArr: [],
        heardContent: [{
          typeDesc: "积分兑换"
        }, {
          typeDesc: "兑换有奖"
        }, {
          typeDesc: "商品积分"
        }],
        exchangCard: [{
          "ImgUrl": "data:image/jpg;base64,",
          "type": 1
        }, {
          "ImgUrl": "data:image/jpg;base64,",
          "type": 2
        }],
        currentIndex: 0,
        hasMore: false,
        pageParams: {
          page: 0
        }
    };
  },

  async onLoad() {
    await this.GetSignInfoF(this.GetSignInfoF.bind(this));
    await this.apiGetInfoFA(this.apiGetInfoFA.bind(this));
  },

  mixins: [],
  components: {},
  props: {},
  methods: {
    goLink() {
      //跳转到商品分类页
      uni.switchTab({
        url: `/pages/classify/index`
      });
    },

    goOrderComment() {
      //跳转到待评价订单列表页
      uni.navigateTo({
        url: `/pages/packageMine/order/index?current=4`
      });
    },

    toTaleDetailA(taleId) {
      uni.navigateTo({
        url: `/pages/packageRecom/taleDetail/index?taleId=${taleId}`
      });
    },

    SignIn() {
      this.apiSignIn(this.apiSignIn.bind(this));
    },

    exchange(type) {
      miniPro.showConfirm({
        title: '提示',
        content: '确定兑换该商品吗？',

        callback(flag) {
          if (!flag) return;
          let param = {
            "vipType": type
          };
          apiExchangF(param).then(msg => {
            miniPro.showToast('兑换成功');
            setTimeout(() => {
              uni.switchTab({
                url: `/pages/mine/index`
              });
            }, 1800);
          }).catch(() => {});
        }

      });
    },

    async apiGetInfoFA(fun) {
      //获取用户信息 查询手机号
      let _self = this;

      await apiInfoF(fun).then(msg => {
        _self.phone = msg.phone;
        _self.sessionKey = msg.sessionKey;
        console.log("打印手机号apiGetInfoFA");
      }).catch(() => {});
    },

    apiSignIn(fun) {
      apiSignInF(fun).then(msg => {
        miniPro.showToast('签到成功');
        setTimeout(() => {
          this.Info = '';
          this.IconArr = [];
          this.dateArr = [];
          this.GetSignInfoF(this.GetSignInfoF.bind(this));
        }, 1500);
      }).catch(() => {});
    },

    async GetSignInfoF() {
      //获取签到信息
      await apiGetSignInfoF().then(msg => {
        this.signToday = msg.signToday;
        this.Info = msg;
        this.IconArr = msg.signCycleIntegrationMap;
        this.dateArr = this.IconArr;
        console.log("嘿嘿GetSignInfoF");
      }).catch(() => {});
    },

    getPhoneNumber(e) {
      let that = this;
      console.log(e);
      console.log(e.detail.errMsg == "getPhoneNumber:ok");
      let iv = e.detail.iv;
      let encryptedData = e.detail.encryptedData;
      let signature = this.$parent.globalData.userInfo.signature;
      let rawData = this.$parent.globalData.userInfo.rawData;
      let sessionKey = this.sessionKey;
      console.log(sessionKey);
      console.log(iv);
      let param = {
        signature: signature,
        rawData: rawData,
        encryptedData: encryptedData,
        iv: iv,
        sessionKey: sessionKey
      };
      apiPasePhoneF({
        miniprogramParam: JSON.stringify(param)
      }).then(res => {
        console.log(res);
        console.log("获取手机号成功调用");
        miniPro.showToast("绑定手机号成功，已获赠100积分");
        setTimeout(() => {
          this.GetSignInfoF(this.GetSignInfoF.bind(this));
          this.apiGetInfoFA(this.apiGetInfoFA.bind(this)); //获取用户信息 查询phone
        }, 2000);
      }).catch(err => {});
    }

  },
  computed: {},
  watch: {
    //监听数据改变
    phone(val) {
      console.log(val);
      this.phone = val;
    }

  }
};
